import React, { Component } from 'react'
import users from '../../data/users';


export class PostHeader extends Component {
  render (){
    return (
      <div className="post-header">
        <div className="post-header-inner">

          <div className="post-header-info">

              <UserAvatar profid={this.props.posthead['poster']} />
              <PostHeaderInfo postdate={this.props.posthead['post-date']} profid={this.props.posthead['poster']}  />
     
          </div>

          <div className="post-header-options">
              <a href="#open-options"> 
                ...  
              </a>
          </div>

        </div>
      </div>
    );
  }
}


function PostHeaderInfo(props){
  return (

    <div className="post-info">
                
      <div className="post-user-name">
          <a href="#userprofile"> {users[props.profid]['user-name']}  </a> 
      </div>

      <div className="post-info-date">
        <a href="#post-url">    {props.postdate} </a> 
      </div>

    </div>

  );
}

function UserAvatar(props){
  return (
    <div className="user-avatar">
        <img src={require("../../assets/img/"+users[props.profid]['user-avatar'])} alt=""/>
    </div>
  );
}

export default PostHeader
