import React, { Component } from 'react'
import postdata from '../../data/posts';
import Post from './Post';
import '../../assets/styles/post-holder.css'


export class PostsHolder extends Component {

  
  render() {
    return (
      <div className="posts-holder">
          <Post  postdata={postdata[0]} />
          <Post  postdata={postdata[1]} />
          <Post  postdata={postdata[2]} />
          <Post  postdata={postdata[3]} />
      </div>
    );

  }
}

export default PostsHolder;
