import React, { Component } from 'react'

export class PostFooter extends Component {
  render (){
    return (
      <div className="post-footer">

        <div className="post-footer-inner">

          <div className="post-footer-stat">

              <a href="#likes" className="likes-count stat-holder">
                  <div className="img">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round" className="feather feather-thumbs-up">
                      <path d="M14 9V5a3 3 0 0 0-3-3l-4 9v11h11.28a2 2 0 0 0 2-1.7l1.38-9a2 2 0 0 0-2-2.3zM7 22H4a2 2 0 0 1-2-2v-7a2 2 0 0 1 2-2h3"></path>
                    </svg>
                  </div>

                  <div className="count">
                    {this.props.poststats['likes']}
                  </div>

              </a>

              <a href="#comment" className="comments-count stat-holder">

                  <div className="img">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round" className="feather feather-message-square">
                      <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"></path>
                    </svg>
                  </div>

                  <div className="count">
                      {this.props.poststats['comments']}
                  </div>
              </a> 

              <a href="#share" className="shares-count stat-holder">

                  <div className="img">
                      <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round" className="feather feather-share-2">
                        <circle cx="18" cy="5" r="3"></circle><circle cx="6" cy="12" r="3"></circle><circle cx="18" cy="19" r="3"></circle><line x1="8.59" y1="13.51" x2="15.42" y2="17.49"></line><line x1="15.41" y1="6.51" x2="8.59" y2="10.49"></line>
                      </svg>
                  </div>

                  <div className="count">
                    {this.props.poststats['shares']}
                  </div>
                  
              </a>            
              
          </div>

          <div className="post-footer-fav">
              <a href="#save-post" className={"fav"+(this.props.poststats['fav']?" active":"" ) } > 
                <div className="img">
                  <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1" strokeLinecap="round" strokeLinejoin="round" className="feather feather-star">
                    <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon>
                  </svg>  
                </div> 
              </a>
          </div>

        </div>
      </div>
    );
  }
}




export default PostFooter

