import React, { Component } from 'react'

export class SiderPart extends Component {
  render() {
    return (
      <div className="sider-holder">
        <div className="sider_inner">

          <div className="sider_logo">
            <h3>
              Fakebook
            </h3>
          </div>

          <div className="sider_nav-holder">
              <div className="slider_nav-holder_title">
                <h4>Menu</h4>
              </div>
              <div className="nav_nodes">
                <ul className="main-nav">

                  <li className="active">                    
                    <a href="#1">
                      <i className="fa fa-newspaper-o"></i>
                      News
                    </a>
                  </li>

                  <li>
                    <a href="#2">
                      <i className="fa fa-newspaper-o"></i>
                      Messages
                    </a>
                  </li>

                  <li>
                    <a href="#3">
                      <i className="fa fa-newspaper-o"></i>
                      Friends
                    </a>
                  </li>

                  <li>
                    <a href="#4">
                      <i className="fa fa-newspaper-o"></i>
                      Pages
                    </a>
                  </li>

                  <li>
                    <a href="#5">
                      <i className="fa fa-newspaper-o"></i>
                      Events
                    </a>
                  </li>

                  <li>
                    <a href="#5">
                      <i className="fa fa-newspaper-o"></i>
                      Manage apps
                    </a>
                  </li>

                </ul>
              </div>
          </div>

        </div>
      </div>
    )
  }
}

export default SiderPart
