import React, { Component } from 'react'
import PostsHolder from './post/PostHolder';
import SiderPart from './sider/SiderPart'


export class MainApp extends Component {
  render() {
    return (
      <React.Fragment >
        <SiderPart />
        <main className="main-cont" >
          <PostsHolder />
        </main>
      </React.Fragment>
    )
  }
}

export default MainApp
