import React, { Component } from 'react'

export class PostContent extends Component {
  render (){
    return (
      <div className="post-content">
        <span className="post-text">
          {this.props.postcont['post-text']}
        </span>
        <PostPict postpict={this.props.postcont['post-pict']} />
      </div>
      
    );
  }
}


function PostPict (props) {

    return (
      <div className="post-pict">
          <a href="#post-url">
          <img src={require("../../assets/img/"+props.postpict)} alt="quotes"/></a>
      </div>
    );

}




export default PostContent
